<template>
  <el-container class="main-container">
    <mavon-editor
      :boxShadow="false"
      defaultOpen="preview"
      :subfield="false"
      :toolbarsFlag="false"
      v-model="txt"
    />
  </el-container>
</template>
<script>
export default {
  name: "Common",
  data() {
    return {
      isHide: false
    };
  },
  created() {
    let _this = this;
    let type = "about";
    this.$http({
      method: "GET",
      url: `https://chenfei-1300076408.cos.ap-guangzhou.myqcloud.com/${type}.md`
    }).then(res => {
      let txt = (res && res.data) || "请检查文件是否存在";
      _this.$store.commit("setTxt", txt);
    });
  },
  computed: {
    txt: {
      get: function() {
        return this.$store.state.txt;
      },
      set: function() {}
    }
  },
  methods: {}
};
</script>
