<template>
  <div class="hello">
    <!-- 侧边导航栏，需要给 具体 导航的 父级 加 class "acts" 则可以修改高亮状态 -->
    <!-- 关于 三级路由  不匹配侧边栏的问题，需要 对 返回的路由对象 进行重新改写,见 computed=> routerli -->
    <el-aside class="menu" width="auto" :class="{ menuHide: isHide }">
      <el-menu
        @select="select"
        :default-active="$route.path.slice(0, 7)"
        active-text-color="#3f51b5"
        class="menu-wrap"
      >
        <li v-show="showButton">
          <el-button @click="clickMenu" type="primary" circle class="is-circle" key="menusBtn">
            <i class="iconfont" :class="[iconName]"></i>
          </el-button>
        </li>
        <li>
          <div class="brand-wrap">
            <div class="brand">
              <router-link to="/" class="avatar waves-effect waves-circle waves-light">
                <img :src="avatarSrc" />
              </router-link>

              <hgroup class="introduce">
                <div class="nickname">{{config.author}}</div>
                <a :title="config.email " class="mail">{{config.email }}</a>
              </hgroup>
            </div>
          </div>
        </li>
        <el-menu-item index="feichen/about">
          <i class="iconfont icon-about"></i>
          <span class="item-title" slot="title">{{config.menus.about }}</span>
        </el-menu-item>
        <el-menu-item index="feichen/education">
          <i class="iconfont icon-education"></i>
          <span class="item-title" slot="title">{{config.menus.education }}</span>
        </el-menu-item>
        <el-menu-item index="feichen/publication">
          <i class="iconfont icon-wenzhang"></i>
          <span class="item-title" slot="title">{{config.menus.publication }}</span>
        </el-menu-item>
        <el-menu-item index="feichen/talk">
          <i class="iconfont icon-talk"></i>
          <span class="item-title" slot="title">{{config.menus.talk }}</span>
        </el-menu-item>
        <el-menu-item index="feichen/collaborators">
          <i class="iconfont icon-collaborators"></i>
          <span class="item-title" slot="title">{{config.menus.collaborators }}</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
  </div>
</template>

<script>
const width =
  window.innerWidth ||
  document.documentElement.clientWidth ||
  document.body.clientWidth;
const isMobile = width < 1190 ? true : false;
export default {
  name: "Aside",
  data() {
    return {
      config: {
        title: "Fei CHEN",
        repo: "https://rayprojects.github.io/feichen", // 添加 github 链接
        author: "Fei CHEN", //侧边栏的设置
        email: "chenfeianthony@gmail.com",
        menus: {
          about: "About", //第一个导航
          education: "Education", //第二个
          publication: "Publication",
          talk: "Invited Talk/Seminar",
          collaborators: "Collaborators"
        }
      },
      avatarSrc: "/feichen/avatar.jpg",
      showButton: !isMobile,
      isHide: isMobile
    };
  },
  computed: {
    iconName() {
      return this.isHide ? "icon-caidan" : "icon-guanbi";
    }
  },
  methods: {
    select(key) {
      this.$router.push(key);
      this.readFormTencentCloudCos(key);
    },
    readFormTencentCloudCos: function(key) {
      let _this = this;
      let type = key.split("/")[1];
      this.$http({
        method: "GET",
        url: `https://chenfei-1300076408.cos.ap-guangzhou.myqcloud.com/${type}.md`
      }).then(res => {
        let txt = (res && res.data) || "请检查文件是否存在";
        _this.$store.commit("setTxt", txt);
      });
    },
    clickMenu() {
      if (!isMobile) {
        this.isHide = !this.isHide;
      }
    }
  }
};
</script>

